<template>
  <!-- 主体 -->
  <div class="main-wrapper">
    <!-- 左侧 -->
    <div class="side-wrapper" >
      <div class="slide-wrapper-tow" style="width: 216px;left:0">
        <!-- 左侧导航 -->
        <nav-bar style="padding-bottom: 80px">
          <Menu ref="rightMenu" width="100%" theme="dark" :accordion="false" @on-select="selectFn" :active-name="$route.path">
            <Menu-item   name="/query">
              <span class="queryMenuEllipsis">{{ $t('query') }}</span>
            </Menu-item>
          </Menu>
        </nav-bar>
        <!-- 左侧导航 -->
      </div>
    </div>
    <!-- 左侧 /-->
    <!-- 内容部分 -->
    <div class="main-container">
      <container>
        <div class="breadData">
          <!-- 面包屑 -->
          <div class="breadcrumb" >
            <Breadcrumb separator=">">
              <span>{{ $t('path') }}：</span>
              <Breadcrumb-item >
                <!--<Icon type="ios-home-outline"></Icon>-->
                {{ $t('home') }}
              </Breadcrumb-item>
              <Breadcrumb-item >
                <!--<Icon :type="pound"></Icon>-->
                {{ $t('query') }}
              </Breadcrumb-item>
            </Breadcrumb>
          </div>
          <!-- 面包屑 /-->
          <div class="list breadList">
            <!-- 搜索 v-show="state.searchState.show"-->
            <div class="searchData">
              <div class="listDataPath">
                <div class="pull-left">
                  <a>
                    <img :src="require('@/assets/img/u191.png')" alt="">
                    <span>导出报表</span>
                  </a>
                </div>
              </div>
              <div class="listHead">
                <h4 class="pull-center">
                  查询统计
                </h4>
              </div>
              <Form :label-width="80" inline>
                <div class="clearfix xsAuto" style="padding:10px;text-align: center">
                  <Row class="listFormInput">
                    <Col span="12" >
                    <Row >
                      <Col span="24">
                      <Form-item label="">
                        <Checkbox size="large" v-model="params.category">产品属性</Checkbox>
                        <Select v-model="params.category" placeholder="请选择" style="width:187px">
                          <Option value="一代标签">一代标签</Option>
                          <Option value="二代标签">二代标签</Option>
                        </Select>
                      </Form-item>
                      </Col>
                      <Col span="24">
                      <Form-item label="">
                        <Checkbox size="large" v-model="params.category">产品名称</Checkbox>
                        <Select v-model="params.category" placeholder="请选择" style="width:187px">
                          <Option value=""></Option>
                          <Option value=""></Option>
                        </Select>
                      </Form-item>
                      </Col>
                      <Col span="24">
                      <Form-item label="">
                        <Checkbox  size="large" v-model="params.category">产品型号</Checkbox>
                        <Select v-model="params.category" placeholder="请选择" style="width:187px">
                          <Option value=""></Option>
                          <Option value=""></Option>
                        </Select>
                      </Form-item>
                      </Col>
                    </Row>
                    </Col>
                    <Col span="10" >
                    <Button   size="large" style="margin-right: 30px;background: #e1e0de;margin-top: 60px">
                      <Icon type="stats-bars" style="vertical-align: bottom" size="20" ></Icon>
                      汇总方式
                    </Button>
                    <Button  size="large" style="background: #e1e0de;margin-top: 60px">
                      <Icon type="android-list" style="vertical-align: bottom" size="20"></Icon>
                      明细方式
                    </Button>
                    </Col>
                  </Row>
                </div>
              </Form>
            </div>
            <!-- 搜索 /-->
            <div class="listData">
              <Table :loading="loading2"   :size="tableSize"
                     :data="listData"
                     :border="true"
                     :columns="columns1" ref="table" @on-select="onSelect" @on-selection-change="onSelectionChange"></Table>
              <!--<Page :total="100" show-sizer show-elevator @on-change="pageChange" style="margin-top: 10px"-->
              <!--@on-page-size-change="PageSizeChange"></Page>-->
            </div>
          </div>
        </div>
      </container>
    </div>
    <!-- 内容部分 /-->
  </div>
  <!-- 主体 /-->
</template>
<script>
  import elementResizeDetectorMaker from 'element-resize-detector'
  var erd = elementResizeDetectorMaker()
  import NavBar from '@/components/layout/navbar/NavBar'
  import Container from '@/components/layout/container/Container'
  export default {
    name: 'list',
    components: {
      NavBar,
      Container
    },
    data () {
      return {
        formItem: {
          input: '',
          select: '',
          date: '',
          time: '',
          radio: '',
          checkbox: []
        },
        editModal: false,
        detailModal: false,
        deleteTip: false,
        loading2: false, // 分页loading
        tableSize: 'default', // @:size
        DateReady: false, // 判断异步数据加载完成，避免报错
        loading: false, // save
        currDate: {}, // 当前编辑和新增的行数据
        currIndex: 0, // 当前编辑和新增的行号
        saveDisabled: false,
        params: {
          page: 1,
          limit: 10,
          category: 'Android'
        },
        selection: [], // 表格选中项
        listData: [], // @:data
        columns1: [
          { // @:columns
            type: 'index', // 开启序号
            width: 60,
            align: 'center',
            title: '序号'
          },
          {
            title: '产品属性',
            key: 'who'
          },
          {
            title: '产品名称',
            key: 'who'
          },
          {
            title: '产品型号',
            key: 'type'
          },
          {
            title: '数量',
            key: 'type'
          },
          {
            title: '备注',
            key: 'desc',
            className: 'min-width'
          }
        ]
      }
    },
    watch: {
      /**
       * @params 监听参数变化重新获取数据
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      selectFn (a) {
        this.$router.push({
          path: a
        })
      },
      searchShow () {
        this.searchState = !this.searchState
      },
      /**
       * 刷新页面请求
       * */
      refresh () {
        this.getData(this.params)
      },
      /**
       * @params:category 分类 page：页码 limit:条数
       * */
      getData (params) {
        this.loading2 = false
        this.listData = []
//        this.$api.orderList(params).then((res) => {
//          if (!res.error) {
//            this.listData = res.results
//            this.listData = []
//            this.DateReady = true
//            this.loading2 = false
//          } else {
//            this.$Message.error(res.msg)
//            this.loading2 = false
//          }
//        })
      },
      /**
       * @on-change 页码改变的回调，返回改变后的页码
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 切换每页条数时的回调，返回切换后的每页条数
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 表格对应操作方法
       * @show 查看
       * @remove 删除
       * @edit
       */
      show (index) {
        this.currIndex = index
        this.currDate = this.listData[index]
        this.detailModal = true
        this.$Modal.info({
          title: '详情',
          content: `姓名：${this.listData[index].who}<br>平台：${this.listData[index].type}<br>描述：${this.listData[index].desc}`
        })
      },
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
        this.editModal = true
        this.currIndex = index
        if (index === -1) { // 新增
          this.currDate = {
            createdAt: '',
            desc: '',
            publishedAt: '',
            type: '',
            used: true,
            who: '',
            url: 'c.fwone.com'
          }
        } else { // 编辑
          this.currDate = this.listData[index]
        }
      },
      /**
       * 批量删除
       */
      deleteBatch () {
        this.deleteTip = false
        // ...
      },
      saveBatch () {
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.$Message.info('保存成功')
          this.editModal = false
        }, 3000)
      },
      /**
       * 数据导出
       * @ type 1 原始数据 2过滤数据
       */
      exportData (type) {
        if (type === 1) {
          this.$refs.table.exportCsv({
            filename: '原始数据'
          })
        } else if (type === 2) {
          this.$refs.table.exportCsv({
            filename: '排序和过滤后的数据',
            original: false
          })
        }
      },
      /**
       * 多选
       * selection：已选项数据 row：刚选择的项数据
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      }
    },
    created () {
      this.getData(this.params)
    },
    mounted () {
      erd.listenTo(window, 'resize', this.handleResize)
    }
  }
</script>
<style lang="less" scoped>
  .breadData .breadList .searchData .listFormInput {
    text-align: left;
    margin: 0 0 0 60px;
  }
</style>



